import React, {useContext, useEffect, useState} from "react";
import {BrowserRouter as Router, Link} from "react-router-dom";
import {useCookies} from "react-cookie";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
 import {faHeart, faCompass, faHistory, faCog} from "@fortawesome/free-solid-svg-icons";
import {GlobalContext} from "./GlobalContext";

function Navbar() {
    const {currPage} = useContext(GlobalContext);
    const [currentPage, setCurrentPage] = currPage;
    const [theme, setTheme] = useCookies(['theme']);
    const currentTheme = theme === undefined ? 'light-mode' : theme.theme;

    useEffect( () => {
    });

    function menuClick (menu) {
        setCurrentPage(menu);
    };

    return (
        <footer className='footer'>
            <div id="buttonGroup" className={'btn-group selectors '  + (currentTheme)} role="group">
                <Link onClick={() => menuClick('favorite')} to='/' id="favorite" type="button" className={'btn ' + (currentPage === 'favorite' ? 'button-active ' : 'button-inactive ')}>
                    <div className="selector-holder">
                        <FontAwesomeIcon icon={faHeart} className='align-self-center'/>
                        <span>Favorite</span>
                    </div>
                </Link>
                <Link onClick={() => menuClick('explore')} to='explore' id="explore" type="button" className={'btn ' + (currentPage === 'explore' ? 'button-active ' : 'button-inactive ')}>
                    <div className="selector-holder">
                        <FontAwesomeIcon icon={faCompass} className='align-self-center'/>
                        <span>Explore</span>
                    </div>
                </Link>
                <Link onClick={() => menuClick('history')} to='history' id="history" type="button" className={'btn ' + (currentPage === 'history' ? 'button-active ' : 'button-inactive ')}>
                    <div className="selector-holder">
                        <FontAwesomeIcon icon={faHistory} className='align-self-center'/>
                        <span>History</span>
                    </div>
                </Link>
                <Link onClick={() => menuClick('setting')} to='setting' id="setting" type="button" className={'btn ' + (currentPage === 'setting' ? 'button-active ' : 'button-inactive ')}>
                    <div className="selector-holder">
                        <FontAwesomeIcon icon={faCog} className='align-self-center'/>
                        <span>Setting</span>
                    </div>
                </Link>
            </div>
        </footer>
    )
}

export default Navbar;